import React, {  useEffect } from 'react';
import { Modal, Input, Form } from 'antd';
import { ModalProps } from 'antd/lib/modal';
const FormItem = Form.Item;
export interface EditDepModalProps extends ModalProps {
  width?: string | number,
  visible: boolean;
  onCancel: any;
  currentDataInfo: any;
  onSubmit: any;
  form: any;
}

function EditDepModal(props: EditDepModalProps) {
  const { visible, width, currentDataInfo, onCancel, onSubmit, form } = props;
  const { getFieldDecorator, setFieldsValue } = form;
  useEffect(() => {
    setFieldsValue({//input
      names: currentDataInfo.name
    })
  }, [currentDataInfo.name])// 
 
  return (
    <Modal
      title=""
      visible={visible}
      onCancel={onCancel}
      width={width || 520}
      style={{ height: 600 }}
      onOk={() => onSubmit(form)}
    >
      <Form>
        <FormItem
          wrapperCol={{ span: 15, offset: 1 }}
        >   {getFieldDecorator('names', {
          rules: [
            { required: true, message: '' },
          ],
        })(
          <Input />
        )}
        </FormItem>
      </Form>
    </Modal >
  )
}
export default Form.create<EditDepModalProps>()(EditDepModal);